.baseIcon {
	margin-bottom: 20px;
	transition: all 0.5s;
	color: #fff;
	width: 100px;
	height: 100px;
	padding: 13px 0;
	border-radius: 25%;
	background: linear-gradient(to right, #6372ff 0%, #5ca9fb 100%);
	box-shadow: 10px 10px 10px rgba(0,0,0,.05);
}

.backdropA {
	padding: 100px 0;
	background: #f6f6f6;
}

.backdropB {
	padding: 100px 0;
	background: #eaeaea;
}

.backdropC {
	padding: 100px 0;
	background: linear-gradient(to bottom right, #6372ff 0%, #5ca9fb 100%);
}